import * as types from './mutation-types'

// logo
export const setLogo = ({commit}, logo) => {
    commit(types.SETLOGO, logo)
}

// 
export const checkUnfold = ({commit}) => {
    commit(types.CHECKUNFOLD)
}

// 
export const Fold = ({commit}) => {
    commit(types.FOLD)
}

// 
export const checkTheme = ({commit}, theme) => {
    commit(types.CHECKTHEME, theme)
}

// 
export const storageFold = ({commit}, checked) => {
    commit(types.STORAGEFOLD, checked)
}

// 
export const addToast = ({commit}, msg) => {
    commit(types.ADDTOAST, msg)
}
export const removeToast = ({commit}, index) => {
    commit(types.REMOVETOAST, index)
}



