import Vue from 'vue'
import Vuex from 'vuex'
import * as actions from './actions'
import * as getters from './getters'
import mutations from './mutations'
Vue.use(Vuex)

// 
const state = {
	Logo: '',
	Sidebar: [{
		name: '',
		link: '/config',
		active: true,
		icon: 'iconfont icon-xitongpeizhi'
	}, {
		name: '',
		link: '/merchant',
		active: false,
		icon: 'iconfont icon-shangjia1'
	}, {
		name: '',
		link: '/favorable',
		active: false,
		icon: 'iconfont icon-youhui'
	}, {
		name: '',
		link: '/purchase',
		active: false,
		icon: 'iconfont icon-youhui'
	}],
	Settings: {
		theme: 'black',
		themes: ['white', 'black']
	},
	Unfold: true,
	UnfoldStatus: false,
	Toast: []
}

// 
if(typeof localStorage.SIDEBARFOLD !== 'undefined'){
	let sf = eval(localStorage.SIDEBARFOLD);
	state.Unfold = !sf
	if(sf){ // 
		state.UnfoldStatus = sf
	}
}
if(typeof localStorage.TATHEME !== 'undefined'){
	state.Settings.theme = localStorage.TATHEME
}

// 
function watchResize(width) {
	if(state.UnfoldStatus) { // 
		return false;
	}
	if(width < 1024) {
		state.Unfold = false
	} else {
		state.Unfold = true
	}
}

if(('addEventListener' in document)) {
	document.addEventListener('DOMContentLoaded', function() {
		window.addEventListener('resize', function() {
			watchResize(document.body.clientWidth)
		})
	}, false);
}

if(state.Unfold) {
	watchResize(document.body.clientWidth + 17)
}

//  store 
export default new Vuex.Store({
	actions,
	getters,
	state,
	mutations
})


