<template>
	<div class="am-g am-cf">
		<div class="am-u-sm-12 am-padding-0">

			<div class="am-panel ta-panel">
				<div class="am-panel-hd am-cf">
					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<input type="text" class="am-form-field" v-model="queryData" placeholder="">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button" @click="query">
								
							</button>
						</span>
					</div>

					<div class="am-input-group am-u-md-6 am-u-lg-4">
						<span class="am-input-group-btn">
							<button class="am-btn am-btn-default" type="button">
								
							</button>
						</span>
						<select class="ta-panel-hd-select" v-model="merchant">
							<option v-for="(mData, index) in merchants" :value="mData.id">
								{{mData.name}}
							</option>
						</select>
					</div>

				</div>
				<div class="am-panel-bd">
					<table class="am-table am-table-striped am-table-bordered am-table-hover ta-table am-margin-0">
						<thead>
							<tr>
								<th></th>
								<th></th>
								<th></th>
								<th class="am-hide-sm-only"></th>
								<th class="am-hide-sm-only"></th>
								<th style="max-width: 120px;"></th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="(tData, index) in tableData">
								<template v-if="index < rowSelected">
									<td>{{ tData.number }}</td>
									<td>{{ tData.service }}</td>
									<td>{{ tData.mobile }}</td>
									<td class="am-hide-sm-only">{{ tData.time }}</td>
									<td class="am-hide-sm-only">{{ tData.merchant }}</td>
									<td>
										<div class="am-dropdown ta-sm-show" data-am-dropdown>
											<button class="am-btn am-btn-success am-dropdown-toggle am-padding-xs"> 
												<span class="am-icon-cog"></span>
											</button>
											<ul class="am-dropdown-content ta-dropdown-btn-content am-list">
												<li>
													<a href="javascript:;" @click="edit(index)">
														<span class="am-text-primary">
															<i class="am-icon-edit"></i> 
														</span>
													</a>
												</li>
												<li>
													<a href="javascript:;" @click="remove(index)">
														<span class="am-text-danger">
															<i class="am-icon-remove"></i> 
														</span>
													</a>
												</li>
											</ul>
										</div>
										
										<div class="ta-sm-hide">
											<a href="javascript:;" class="am-text-primary" @click="edit(index)">
												<i class="am-icon-edit"></i> 
											</a>
											<a href="javascript:;" class="am-text-danger" @click="remove(index)">
												<i class="am-icon-remove"></i> 
											</a>
										</div>
									</td>
								</template>
							</tr>
						</tbody>
					</table>
				</div>
				<div class="am-panel-footer">
					<div class="am-cf">
						<ul class="am-pagination ta-pagination am-margin-vertical-0 am-pagination-right">
							<li class="ta-sm-hide">
								<a href="javascript:;"><span class="am-text-danger" v-text="total"></span></a>
							</li>
							<li class="ta-sm-hide">
								<a href="javascript:;" v-on:click="home"></a>
							</li>
							<li v-if="curr > 1">
								<a href="javascript:;" v-on:click="prev"></a>
							</li>
							<li v-if="curr < pages">
								<a href="javascript:;" v-on:click="next"></a>
							</li>
							<li>
								<a href="javascript:;"><span class="am-text-danger" v-text="pages"></span></a>
							</li>
							<li class="ta-pagination-rowTotal">
								<a class="ta-select-page" href="javascript:;">
									<select v-model="rowSelected" v-cloak>
										<option v-for="row in rowTotal" v-bind:value="row">{{row}}</option>
									</select>
								</a>
							</li>
						</ul>
					</div>
				</div>
			</div>

		</div>
	</div>
</template>

<script>
	export default {
		name: 'MerchantList',
		data() {
			return {
				merchant: '', // 
				merchants: [{
					name: '',
					id: ''
				}, {
					name: '-0',
					id: '-0'
				}, {
					name: '-1',
					id: '-1'
				}, {
					name: '-2',
					id: '-2'
				}, {
					name: '-3',
					id: '-3'
				}],
				pages: 0, // 
				total: 0, // 
				curr: 1, // 
				rowTotal: [10, 20, 50, 100], // 
				rowSelected: 10, //  selected 
				tableData: [], // 
				queryData: '' // 
			}
		},
		created() {
			// 
			const self = this;
			if(self.$route.query.id) { // 
				self.merchant = self.$route.query.id
			}
			const data = self.getData(21, self.$route.query.id);
			self.tableData = data;
			self.total = data.length;
			self.pages = Math.ceil(self.total / self.rowSelected); //
		},
		methods: {
			getData(size, id) {
				const self = this;
				const data = [];
				for(let i = 0; i < size; i++) {
					let randomNumber = $.getRandom(6);
					data.push({
						checked: false,
						number: randomNumber,
						mobile: '186102' + randomNumber,
						service: '',
						time: $.getTime(),
						merchant: self.merchant || '-' + $.getRandom(1)
					})
				}
				return data;
			},
			next() {
				this.curr += 1;
			},
			prev() {
				this.curr -= 1;
			},
			home() {
				this.curr = 1;
			},
			query() {
				const queryData = this.queryData;
				if(queryData.trim()) {
					layer.msg('' + queryData)
				}
			},
			edit(index) {
				layer.msg('' + index)
			},
			remove(index) {
				var self = this;
				layer.confirm('?', {
					title: '',
					btn: ['', '']
				}, function() {
					layer.closeAll()
					self.tableData.splice(index, 1)
				})

			}
		}
	}
</script>


